<template>
  <div class="">
    <Button 
      label=""
      class=""
      size="small"
      icon="pi pi-pencil"
      @click="openModal()"
      v-tooltip.left="`Update`"
    />
    <Dialog v-model:visible="visible" :modal="true" class="p-fluid" header="Update" :breakpoints="{ '960px': '80vw' }" :style="{ width: '50vw' }" @hide="visible = false">
      <div class="" style="">
        <div class="flex flex-column gap-2">
          <label for="kode_permohonan">Kode</label>
          <InputText id="kode_permohonan" v-model="dataAsal.kode_permohonan" aria-describedby="kode_permohonan-help" readonly />
        </div>
        <div v-if="dataAsal.kode_permohonan_sebelumnya" class="flex flex-column gap-2 mt-2">
          <label for="kode_permohonan_sebelumnya">Kode Sebelumnya</label>
          <InputText id="kode_permohonan_sebelumnya" v-model="dataAsal.kode_permohonan_sebelumnya" aria-describedby="kode_permohonan_sebelumnya-help" readonly />
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="asal_pemohon">Asal</label>
          <InputText id="asal_pemohon" v-model="dataAsal.asal_pemohon" aria-describedby="asal_pemohon-help" readonly />
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="username">Nama Pemohon</label>
          <InputText id="username" v-model="dataAsal.nama_pemohon" aria-describedby="username-help" readonly />
          <!-- <small id="username-help">{{v$.data.nama_agama.$invalid}}</small> -->
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="username">No Identitas</label>
          <InputText id="username" v-model="dataAsal.no_identitas" aria-describedby="username-help" readonly />
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="no_hp_pemohon">No Handphone</label>
          <InputText id="no_hp_pemohon" v-model="dataAsal.no_identitas" aria-describedby="no_hp_pemohon-help" readonly />
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="nama_kategori">Kategori</label>
          <InputText id="nama_kategori" v-model="dataAsal.nama_kategori" aria-describedby="nama_kategori-help" readonly />
        </div>
        <div v-if="dataAsal.nama_fakultas" class="flex flex-column gap-2 mt-2">
          <label for="nama_fakultas">Fakultas</label>
          <InputText id="nama_fakultas" v-model="dataAsal.nama_fakultas" aria-describedby="nama_fakultas-help" readonly />
        </div>
        <div v-if="dataAsal.nama_prodi" class="flex flex-column gap-2 mt-2">
          <label for="nama_prodi">Prodi</label>
          <InputText id="nama_prodi" v-model="dataAsal.nama_prodi" aria-describedby="nama_prodi-help" readonly />
        </div>
        <div v-if="dataAsal.nama_biro" class="flex flex-column gap-2 mt-2">
          <label for="nama_biro">Biro</label>
          <InputText id="nama_biro" v-model="dataAsal.nama_biro" aria-describedby="nama_biro-help" readonly />
        </div>
        <div v-if="dataAsal.foto_identitas" class="flex flex-column gap-2 mt-2">
          <label for="username">Foto</label>
          <Image class="text-center" :src="$store.state.ipBackend + '/' + dataAsal.foto_identitas" alt="Image" width="250" preview />
        </div>
        <div v-if="dataAsal.file_laporan1 || dataAsal.file_laporan2" class="flex flex-column gap-2 mt-2">
          <label for="username">File</label>
          <div v-if="dataAsal.file_laporan1" class="p-inputgroup flex-1">
            <Button label="open" @click="openNewTab(dataAsal.file_laporan1)"/>
            <InputText id="file_laporan1" v-model="dataAsal.file_laporan1" aria-describedby="file_laporan1-help" readonly/>
          </div>
          <div v-if="dataAsal.file_laporan2" class="p-inputgroup flex-1">
            <Button label="open" @click="openNewTab(dataAsal.file_laporan2)"/>
            <InputText id="file_laporan2" v-model="dataAsal.file_laporan2" aria-describedby="file_laporan2-help" readonly/>
          </div>
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="username">Deskripsi</label>
          <Textarea id="username" v-model="dataAsal.deskripsi_permohonan" aria-describedby="username-help" readonly />
        </div>
        <hr class="">
        <div v-if="data.catatan_operator" class="flex flex-column gap-2 mb-2">
          <label for="catatan_operator">Catatan Operator</label>
          <Textarea id="catatan_operator" v-model="dataAsal.catatan_operator" aria-describedby="catatan_operator-help" readonly />
        </div>
        <div v-if="data.catatan_supervisor" class="flex flex-column gap-2 mb-2">
          <label for="catatan_supervisor">Catatan Supervisor</label>
          <Textarea id="catatan_supervisor" v-model="dataAsal.catatan_supervisor" aria-describedby="catatan_supervisor-help" readonly />
        </div>
        <div v-if="data.catatan_manajer" class="flex flex-column gap-2 mb-2">
          <label for="catatan_manajer">Catatan Manager</label>
          <Textarea id="catatan_manajer" v-model="dataAsal.catatan_manajer" aria-describedby="catatan_manajer-help" readonly />
        </div>
        <div class="flex flex-column gap-2" v-if="dataAsal.is_publish != null">
          <label for="is_publish">Publish</label>
          <div id="is_publish" class="">{{dataAsal.is_publish ? 'Ya' : 'Tidak'}}</div>
        </div>
        <div class="flex flex-column gap-2" v-if="dataAsal.is_setuju != 0">
          <label for="is_setuju">Publish</label>
          <div id="is_setuju" class="">{{dataAsal.is_setuju == 1 ? 'Setuju' : 'Tidak Setuju'}}</div>
        </div>
        <div class="flex flex-column gap-2" v-if="dataAsal.status_permohonan < 4">
          <label for="username">Catatan</label>
          <Textarea id="username" v-model="dataForm.catatan" aria-describedby="username-help" :class="{'p-invalid': v$.dataForm.catatan.$invalid}"/>
        </div>
        <div v-if="dataAsal.status_permohonan == 3" class="flex flex-column gap-2 mt-2">
          <label for="catatan_manajer">Publish</label>
          <div class="flex flex-wrap gap-3">
            <div class="flex align-items-center">
              <RadioButton v-model="dataForm.is_publish" inputId="publish-ya" name="Ya" :value="true" :disabled="dataAsal.status_permohonan != 3"/>
              <label for="publish-ya" class="ml-2">Ya</label>
            </div>
            <div class="flex align-items-center">
              <RadioButton v-model="dataForm.is_publish" inputId="publish-tidak" name="Tidak" :value="false" :disabled="dataAsal.status_permohonan != 3"/>
              <label for="publish-tidak" class="ml-2">Tidak</label>
            </div>
          </div>
        </div>
      </div>
      <template #footer>
        <div class="flex justify-content-end" v-if="dataAsal.status_permohonan < 4">
          <Button 
            label="Tolak"
            class=""
            size="small"
            :disabled="loading"
            v-tooltip.left="`tolak`"
            @click="submitTolak()"
          />
          <Button 
            v-if="dataAsal.status_permohonan < 3"
            label="Setujui"
            class=""
            size="small"
            :disabled="!isValid || loading"
            v-tooltip.left="`setujui`"
            @click="submit()"
          />
          <Button 
            v-if="dataAsal.status_permohonan == 3"
            label="Setujui Manager"
            class=""
            size="small"
            :disabled="!isValid || loading"
            v-tooltip.left="`setujui manager`"
            @click="submitJawabanManager()"
          />
        </div>
      </template>
    </Dialog>
  </div>
</template>

<script>
import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'
export default {
  props: [ 'data' ],
  emits: [ 'refresh' ],
  components: {
  },
  setup () {
    return { v$: useVuelidate() }
  },
  data() {
    return {
      loading: false,
      visible: false,
      dataAsal: {},
      dataForm: {
        catatan: null,
        is_publish: true,
      },
    };
  },
  computed: {
    isValid() {
      return !this.v$.dataForm.$invalid;
    },
    isDirty() {
      return this.v$.dataForm.$anyDirty;
    },
  },
  validations () {
    return {
      dataForm: {
        catatan: { required },
      }
    }
  },
  mounted() {
    // const vm = this
  },
  methods: {
    test(){
      // const vm = this
    },
    openNewTab(path){
      window.open(this.$store.state.ipBackend + '/' + path)
    },
    openModal(){
      const vm = this
      vm.visible = true
      vm.dataAsal = vm.data
      vm.dataForm = {
        catatan: null,
        is_publish: true,
      }
      vm.v$.$reset();
    },
    async submit(){
      const vm = this
      vm.loading = true
      const data = {}
      switch (vm.data.status_permohonan) {
        case 1:
          data.status_permohonan = 2
          data.catatan_operator = vm.dataForm.catatan
          break;
        case 2:
          data.status_permohonan = 3
          data.catatan_supervisor = vm.dataForm.catatan
          break;
        // case 3:
        //   data.status_permohonan = 4
        //   data.catatan_manajer = vm.dataForm.catatan
        //   data.is_publish = vm.dataForm.is_publish
        //   data.date_catatan_manajer = vm.$moment()
        //   // data.date_catatan_manajer = vm.$moment().format('YYYY-MM-DD')
        //   break;
        default:
          break;
      }
      const res = await vm.$axios.post('permohonan/update', {
        id: vm.data.permohonan_id,
        ...data,
      })
      if(res.data.status == 200){
        vm.visible = false
        vm.$emit('refresh')
        vm.$toast.add({ severity: 'success', summary: 'Konfirmasi', detail: 'Berhasil Update Permohonan', life: 3000 });
      }else{
        vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Gagal Update Permohonan', life: 3000 });
      }
      vm.loading = true
    },
    async submitTolak(){
      const vm = this
      vm.loading = true
      const res = await vm.$axios.post('permohonan/update', {
        id: vm.data.permohonan_id,
        status_permohonan: 0,
      })
      if(res.data.status == 200){
        vm.visible = false
        vm.$emit('refresh')
        vm.$toast.add({ severity: 'success', summary: 'Konfirmasi', detail: 'Berhasil Menolak Permohonan', life: 3000 });
      }else{
        vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Gagal Menolak Permohonan', life: 3000 });
      }
      vm.loading = true
    },
    async submitJawabanManager(){
      const vm = this
      vm.loading = true
      const res = await vm.$axios.post('permohonan/jawaban_manajer', {
        ...vm.data,
        id: vm.data.permohonan_id,
        status_permohonan: 4,
        catatan_manajer: vm.dataForm.catatan,
        is_publish: vm.dataForm.is_publish,
        date_catatan_manajer: vm.$moment(),
      })
      if(res.data.status == 200){
        vm.visible = false
        vm.$emit('refresh')
        vm.$toast.add({ severity: 'success', summary: 'Konfirmasi', detail: 'Berhasil Membuat Jawaban Manager', life: 3000 });
      }else{
        vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Gagal Membuat Jawaban Manager', life: 3000 });
      }
      vm.loading = true
    },
  },
};
</script>